<template>
  <div class="go-mobifone">
    <article-header :header="header"></article-header>
    <div class="article-container">
      <div class="row text-justify">
        <h4 class="et-text-heading-1">Ưu đãi thành viên 8Trip (chỉ áp dụng cho thuê bao mạng MobiFone)</h4>

        <br>
        <p><strong><i>Giới thiệu</i></strong></p>
        <p>
        8Trip ra đời với mong muốn mang lại một công cụ tìm kiếm, lên kế hoạch cho chuyến đi của khách hàng tốt nhất,
        tiết kiệm chi phí nhất bằng việc tìm ra hành trình phù hợp nhất, rẻ nhất, khách sạn giá tốt nhất, xe đưa đón
        sang trọng giá cả hợp lý.
        </p>

        <h5 class="text-italic et-text-highlight">Các dịch vụ tại 8Trip</h5>

        <ul>
          <li>Vé máy bay trong nước và quốc tế giá rẻ (8Trip kết nối với 3 hãng hàng không trong nước và hơn 100 hãng
            hàng không trên thế giới)
          </li>
          <li>Xe sân bay giá rẻ</li>
          <li>Khách sạn giá tốt nhất</li>
          <li>Tour du lịch với nhiều ưu đãi</li>
        </ul>

        <h5 class="text-italic et-text-highlight">Các mức ưu đãi</h5>

        <div class="pricing">
          <pricing-table></pricing-table>
        </div>

        <ul>

          <li>Thuê bao nếu đã là thành viên 8Trip, hệ thống sẽ gừi tin nhắn thông báo việc đăng ký thành công. Nếu thuê
            bao chưa tham gia thành viên, hệ thống sẽ gửi tin nhắn thông báo đăng ký thành công và kèm thông tin
            password truy cập 8Trip. Tài khoản truy cập là số điện thoại của quý khách. Tài khoản và mật khẩu được sử
            dụng để truy cập 8Trip qua wifi. Khách hàng là thành viên truy cập qua 3G hoặc đăng nhập tài khoản nếu dùng
            wifi để được hưởng chính sách ưu đãi dành cho thành viên.
          </li>
        </ul>
        <h5 class="text-italic et-text-highlight-3">Khuyến mại</h5>
        <ul>
          <li>Tặng 100.000 VNĐ vào tài khoản khi lần đầu đăng ký thành viên 8Trip, chương trình áp dụng từ ngày
            18/06/2018 đến hết ngày 18/06/2019. Với thành viên có điểm tích lũy >=10 điểm thì tiền trong tài khoản 8Trip
            được trừ khi thanh toán dịch vụ tại 8Trip.
          </li>
          <li>Tặng xe đưa sân bay Hà Nội - Nội Bài đối với hành trình khứ hồi từ 4 người trở lên, chương trình áp dụng
            đối với đơn đặt vé đến hết ngày 18/06/2019.
          </li>
        </ul>

        <h5 class="text-italic et-text-highlight">Quy định</h5>

        <ul>
          <li>Tại một thời điểm, khách hàng chỉ có thể đăng ký tham gia thành viên ở một trong ba loại thành viên theo
            quy định của 8Trip: thành viên VIP, thành viên VIP30, thành viên VIP365
          </li>

          <li>Đối với thuê bao khóa 1 chiều, 2 chiều sẽ không được gia hạn thành viên. Khi thuê bao được mở khóa, sẽ
            phải đăng ký lại thành viên mới được hưởng ưu đãi thành viên. Điểm tích lũy cũ vẫn được cộng vào khi đăng ký
            thành viên lại.
          </li>
        </ul>


        <h5 class="text-italic et-text-highlight-3">Mã giảm giá</h5>

        <ul>
          <li>Thành viên 8Trip được cấp một mã giảm giá chính là số điện thoại đăng ký thành viên. Bạn có thể tặng bạn
            bè mã giảm giá khi đặt vé máy bay tại 8Trip. Mức giảm sau khi nhập mã giảm giá là 90,000 VNĐ/vé. Nếu đơn
            hàng được thanh toán bạn sẽ được cộng 30,000 VNĐ vào tài khoản 8Trip.
          </li>

          <li>Với thành viên có điểm tích lũy >=10 điểm thì số tiền trong tài khoản 8Trip sẽ được trừ khi bạn thanh toán
            dịch vụ tại 8Trip. Ví dụ: khi đặt vé máy bay tại 8Trip tiền thanh toán của bạn là 2,200,000 VNĐ, trong tài
            khoản 8Trip của bạn hiện có 1,000,000 thì bạn chỉ phải thanh toán 1,200,000 VNĐ và tài khoản 8Trip của bạn
            sẽ là 0 VNĐ. Chú ý: mã giảm giá không áp dụng cho thành viên 8Trip.
          </li>


        </ul>

        <h5 class="text-italic et-text-highlight-3">Truy cập dịch vụ để nhận ưu đãi</h5>

        <ul>
          <li>Thành viên 8Trip truy cập vào website của 8Trip qua wifi hoặc 3G nhập thông tin đăng nhập: tài khoản (số
            thuê bao của quý khách), mật khẩu (trong bản tin sms gửi về khi đăng ký thành viên thành công)
          </li>

          <li>Sau khi đăng nhập hệ thông sẽ tự động hiển thị giá ưu đãi dành cho thành viên 8Trip, nếu không truy cập hệ
            thống sẽ hiển thị mức giá dành cho khách hàng không phải là thành viên 8Trip
          </li>
        </ul>


        <p class="text-uppercase"><strong>Công Ty Cổ Phần Truyền Thông Sông Sáng</strong></p>
        <ul>
          <li>Địa chỉ: <strong>567A Lạc Long Quân, Phường Xuân La, Quận Tây Hồ, TP Hà Nội</strong></li>
          <li>Email: <strong><a href="mailto:8trip@ssmedia.vn?Subject=Kính%20gửi%20công%20ty%20Sông%20Sáng">8trip@ssmedia.vn</a></strong>
          </li>
          <li>Hotline: <strong>0909.475.688</strong></li>
        </ul>

      </div>

    </div>

  </div>

</template>

<script>
  import ArticleHeader from './ArticleHeader'
  import PricingTable from './PricingTable'

  export default {
    name: "Mobifone",
    data() {
      return {
        header: {
          title: "8Go x Mobifone",
          subtitle: "Ưu đãi thành viên 8Go cho thuê bao Mobifone"
        }
      }
    },
    components: {
      ArticleHeader,
      PricingTable
    }
  }
</script>

<style scoped>
  @import "../../assets/css/homepage/8go_mobifone.css";
</style>



