            <div><h4><strong>BỘ QUY TẮC ỨNG XỬ  – QUY ĐỊNH CỦA LÁI XE</strong></h4></div>
            <div class="content text-justify">
                <p>8Go duy tr&igrave; bộ quy tắc ứng xử d&agrave;nh cho tất cả đối t&aacute;c t&agrave;i xế để họ c&oacute; một hiểu biết r&otilde; r&agrave;ng về việc tu&acirc;n thủ ph&aacute;p luật, an to&agrave;n, an ninh v&agrave; ti&ecirc;u chuẩn dịch vụ họ cần c&oacute;. V&igrave; 8Go cam kết một ch&iacute;nh s&aacute;ch kh&ocirc;ng thỏa hiệp với những h&agrave;nh vi vi phạm, những h&agrave;nh vi n&agrave;y c&oacute; thể dẫn tới việc t&agrave;i khoản của t&agrave;i xế bị chặn tạm thời hoặc vĩnh viễn khỏi ứng dụng 8Go.</p>
                <p>Mỗi t&agrave;i xế 8Go sẽ phải tu&acirc;n theo bộ quy tắc ứng xử sau đ&acirc;y:</p>
                <h4><strong>1.&nbsp;&nbsp;TU&Acirc;N THỦ LUẬT, QUY ĐỊNH, CH&Iacute;NH S&Aacute;CH V&Agrave; C&Aacute;C HƯỚNG DẪN CỦA 8GO.</strong></h4>
