                    <li>T&agrave;i xế cam kết kh&ocirc;ng tham gia bất kỳ hoạt động n&agrave;o l&agrave;m ảnh hưởng hoặc tổn hại đến danh tiếng v&agrave;/hoặc l&agrave;m gi&aacute;n đoạn hoạt động của 8Go, đồng thời kh&ocirc;ng cấu kết với c&aacute;c t&agrave;i xế kh&aacute;c hoặc b&ecirc;n thứ ba nhằm mục đ&iacute;ch thực hiện c&aacute;c h&agrave;nh vi n&ecirc;u tr&ecirc;n.</li>
