                    <li>T&agrave;i xế được khuyến kh&iacute;ch nắm r&otilde; c&aacute;c diễn biến tr&ecirc;n đường. T&agrave;i xế sẽ hoạt động thuận lợi hơn khi nắm được c&aacute;c điều kiện giao th&ocirc;ng như c&ocirc;ng trường n&agrave;o đang thi c&ocirc;ng, c&oacute; c&aacute;c vụ tai nạn lớn hoặc c&aacute;c sự kiện ngo&agrave;i trời hay kh&ocirc;ng, nhằm tr&aacute;nh ảnh hưởng đến lộ tr&igrave;nh.</li>
