                    <li>Tr&acirc;n trọng cuốc xe m&agrave; t&agrave;i xế nhận được. Điều n&agrave;y v&ocirc; c&ugrave;ng quan trọng trong việc x&acirc;y dựng sự tin tưởng của kh&aacute;ch h&agrave;ng v&agrave; khuyến kh&iacute;ch nhiều h&agrave;nh kh&aacute;ch sử dụng dịch vụ G8o hơn nữa. Nhiều h&agrave;nh kh&aacute;ch hơn c&oacute; nghĩa l&agrave; nhiều cuốc xe hơn cho to&agrave;n thể t&agrave;i xế.</li>
