                    <li>Chờ kh&aacute;ch h&agrave;ng đến điểm đ&oacute;n. Nếu t&agrave;i xế bỏ đi m&agrave; kh&ocirc;ng chờ họ, họ sẽ c&oacute; trải nghiệm ti&ecirc;u cực đối với dịch vụ của 8Go. Vui l&ograve;ng gọi cho h&agrave;nh kh&aacute;ch sau khi đợi 5 ph&uacute;t m&agrave; họ kh&ocirc;ng đến để x&aacute;c nhận xem h&agrave;nh kh&aacute;ch c&oacute; muốn tiếp tục đi xe hay kh&ocirc;ng. H&atilde;y b&aacute;o cho h&agrave;nh kh&aacute;ch v&agrave; bộ phận chăm s&oacute;c kh&aacute;ch h&agrave;ng nếu t&agrave;i xế kh&ocirc;ng thể tiếp tục đợi.</li>
                    <li>Kh&ocirc;ng nhận cuốc xe khi tr&ecirc;n xe đ&atilde; c&oacute; kh&aacute;ch v&igrave; điều n&agrave;y sẽ khiến kh&aacute;ch h&agrave;ng mới phải đợi l&acirc;u hơn.</li>
                    <li>T&agrave;i xế kh&ocirc;ng được ph&eacute;p từ chối cuốc xe dựa tr&ecirc;n những đặc điểm c&aacute; nh&acirc;n của kh&aacute;ch như chủng tộc, t&ocirc;n gi&aacute;o, quốc tịch, khuyết tật, xu hướng t&igrave;nh dục, giới t&iacute;nh, t&igrave;nh trạng h&ocirc;n nh&acirc;n, đặc điểm giới t&iacute;nh, tuổi t&aacute;c hay bất kỳ đặc t&iacute;nh n&agrave;o được bảo vệ bởi ph&aacute;p luật. H&agrave;nh động n&agrave;y c&oacute; thể dẫn đến việc t&agrave;i xế bị chặn vĩnh viễn khỏi ứng dụng 8Go. Điều n&agrave;y thể hiện sự thiếu t&ocirc;n trọng, g&acirc;y n&ecirc;n sự k&igrave; thị v&agrave; ảnh hưởng đến quyền lợi của một c&aacute; nh&acirc;n hoặc một nh&oacute;m người n&agrave;o đ&oacute;, kh&ocirc;ng cần biết h&agrave;nh kh&aacute;ch c&oacute; thuộc nh&oacute;m người đ&oacute; hay kh&ocirc;ng.</li>
                    <li>Kh&ocirc;ng tự &yacute; đưa ra mức ph&iacute; (v&iacute; dụ phụ thu mỗi chuyến đi) bởi đ&oacute; l&agrave; h&agrave;nh vi tăng gi&aacute; tr&aacute;i qui định. Chỉ nhận đ&uacute;ng số tiền cước dựa tr&ecirc;n đồng hồ c&ugrave;ng với c&aacute;c khoản thu đưa ra bởi c&aacute;c c&ocirc;ng ty uy t&iacute;n hoặc dựa tr&ecirc;n ứng dụng.</li>
