                    <li>T&agrave;i xế bị nghi&ecirc;m cấm li&ecirc;n lạc với h&agrave;nh kh&aacute;ch qua bất kỳ k&ecirc;nh li&ecirc;n lạc hay ứng dụng n&agrave;o kh&aacute;c ngo&agrave;i mục đ&iacute;ch c&ocirc;ng việc.</li>
                    <li>Nhấn &ldquo;Trả kh&aacute;ch&rdquo;/ &ldquo;Kết th&uacute;c chuyến đi&rdquo; chỉ sau khi h&agrave;nh kh&aacute;ch xuống xe.</li>
                    <li>Nhanh ch&oacute;ng b&aacute;o c&aacute;o th&ocirc;ng tin khẩn cấp đến 8Go. Ch&uacute;ng t&ocirc;i mong muốn gi&uacute;p đỡ trong mọi vấn đề ph&aacute;t sinh.</li>
                    <li>Bất cứ l&uacute;c n&agrave;o t&agrave;i xế thấy đang đối diện với t&igrave;nh huống cần sự gi&uacute;p đỡ khẩn cấp, vui l&ograve;ng gọi số dịch vụ khẩn cấp tại khu vực. Khi tất cả c&aacute;c b&ecirc;n li&ecirc;n quan đ&atilde; an to&agrave;n v&agrave; t&igrave;nh huống được giải quyết bởi lực lượng chức năng, h&atilde;y th&ocirc;ng b&aacute;o cho G8o. Ch&uacute;ng t&ocirc;i sẽ hỗ trợ v&agrave; c&oacute; động th&aacute;i ph&ugrave; hợp nếu cần thiết, bao gồm việc hợp t&aacute;c với c&aacute;c cơ quan ph&aacute;p luật.</li>
                    <li>Tiếp thu những g&oacute;p &yacute; v&agrave; tham gia c&aacute;c kh&oacute;a đ&agrave;o tạo. Tại 8Go, ch&uacute;ng t&ocirc;i cam kết kh&ocirc;ng ngừng cải thiện chất lượng dịch vụ của m&igrave;nh.</li>
                    <li>8Go triển khai hệ thống nhận phản hồi 2 chiều từ t&agrave;i xế v&agrave; h&agrave;nh kh&aacute;ch, cũng như hệ thống kiểm tra chất lượng của c&aacute;c h&agrave;nh kh&aacute;ch b&iacute; mật. Ch&uacute;ng t&ocirc;i cũng thực hiện hệ thống đ&aacute;nh gi&aacute; mức độ ph&ugrave; hợp v&agrave; biểu hiện của t&agrave;i xế li&ecirc;n tục, sau đ&oacute; đưa ra c&aacute;c phản hồi t&ugrave;y theo từng trường hợp. Đa số trường hợp, ch&uacute;ng t&ocirc;i hướng đến l&agrave;m việc với t&agrave;i xế qua c&aacute;c buổi họp n&ecirc;u &yacute; kiến, c&aacute;c buổi đ&agrave;o tạo, c&aacute;c lớp kĩ năng v&agrave; kiểm tra chất lượng,&hellip;Kết quả từ c&aacute;c chương tr&igrave;nh n&agrave;y t&ugrave;y thuộc v&agrave;o khả năng tiếp thu của c&aacute;c t&agrave;i xế.</li>
                    <li>T&agrave;i xế bắt buộc tham gia c&aacute;c buổi đ&agrave;o tạo được tổ chức bởi 8Go hoặc c&aacute;c đối t&aacute;c của 8Go, trừ trường hợp khẩn cấp hoặc l&yacute; do bất khả kh&aacute;ng. Trong những trường hợp đ&oacute;, 8Go sẽ sắp xếp c&aacute;c buổi thay thế v&agrave;o một ng&agrave;y kh&aacute;c.</li>
                </ol>
                <h4><strong>3. THỦ TỤC B&Aacute;O C&Aacute;O T&Igrave;NH HUỐNG KHẨN CẤP</strong></h4>
                <h4><strong>4. CẢI THIỆN V&Agrave; KH&Ocirc;NG NGỪNG TRAU DỒI BẢN TH&Acirc;N</strong></h4>
                <p>Tất cả t&agrave;i xế phải x&aacute;c nhận rằng họ hiểu r&otilde; tất cả c&aacute;c điều khoản trong bộ quy tắc ứng xử của t&agrave;i xế n&ecirc;u tr&ecirc;n v&agrave; phải chịu tr&aacute;ch nhiệm cho bất kỳ việc l&agrave;m l&agrave; kết quả hoặc được x&aacute;c nhận l&agrave; kết quả của qu&aacute; tr&igrave;nh hoạt động dịch vụ di chuyển bằng xe 4 b&aacute;nh, bao gồm v&agrave; kh&ocirc;ng chỉ c&oacute; thiệt hại về t&iacute;nh mạng v&agrave; t&agrave;i sản hay chấn thương c&aacute; nh&acirc;n. T&agrave;i xế cũng sẽ bị qui tr&aacute;ch nhiệm đối với c&aacute;c thiệt hại 8Go phải chịu nếu bị ph&aacute;t hiện vi phạm bất kỳ điều khoản n&agrave;o trong bộ quy tắc ứng xử.</p>
                <p>Hơn nữa, t&agrave;i xế phải hiểu v&agrave; đồng &yacute; rằng 8Go c&oacute; quyền xử l&yacute; những h&agrave;nh vi vi phạm, bao gồm việc chặn tạm thời hoặc vĩnh viễn t&agrave;i xế khỏi ứng dụng 8Go nếu t&agrave;i xế vi phạm bất kỳ điều khoản n&agrave;o của bộ quy tắc ứng xử n&agrave;y.</p>
            </div>
        </div>
    </div>
</template>

<script>
    import ArticleHeader from './ArticleHeader'
    export default {
        name: "Regulation",
        components: {
            ArticleHeader
        },
        data() {
            return {
                header: {
                    title: "BỘ QUY TẮC ỨNG XỬ – QUY ĐỊNH CỦA LÁI XE",
                    subtitle: ""
                },
            }
        }
    }
</script>

